import React from 'react';

import Styles from './styles/index.module.scss';

import { useRegistration } from './hooks/useRegistration';
import { TextInput } from '../formComponents/textInput';
import { SelectButton } from '../formComponents/selectButton';

export const Registration = () => {
  const {
    handleSubmit,
    getFieldProps,
    getFieldMeta,
    dirty,
    isValid,
    setFieldValue,
    resetForm,
    initialValues,
  } = useRegistration();

  return (
    <section className={Styles.registration}>
      <div className={Styles.left}>
        <form onSubmit={handleSubmit}>
          <div className={Styles.content}>
            <h1></h1>
            <SelectButton
              meta={getFieldMeta()}
              {...getFieldProps()}
              setFieldValue={setFieldValue}
              className={Styles.gender}
              error={Styles.error}
              male={Styles.male}
              female={Styles.female}
              selected={Styles.selected}
            />
            <TextInput
              label=""
              type="email"
              name="email"
              placeholder="  email"
              meta={getFieldMeta("email")}
              {...getFieldProps("email")}
              className={Styles.inputRow}
              error={Styles.error}
            />
            <TextInput
              label=""
              type="text"
              name="fname"
              placeholder="  "
              meta={getFieldMeta("fname")}
              {...getFieldProps("fname")}
              className={Styles.inputRow}
              error={Styles.error}
            />
            <TextInput
              label=""
              type="text"
              name="lname"
              placeholder="  "
              meta={getFieldMeta("lname")}
              {...getFieldProps("lname")}
              className={Styles.inputRow}
              error={Styles.error}
            />
            <TextInput
              label=""
              type="password"
              name="password"
              placeholder="  "
              meta={getFieldMeta("password")}
              {...getFieldProps("password")}
              className={Styles.inputRow}
              error={Styles.error}
            />
            <TextInput
              label=""
              type="number"
              name="age"
              placeholder="  "
              meta={getFieldMeta("age")}
              {...getFieldProps("age")}
              className={Styles.inputRow}
              error={Styles.error}
            />
            <TextInput
              label=""
              type="number"
              name="height"
              placeholder="  "
              meta={getFieldMeta("height")}
              {...getFieldProps("height")}
              className={Styles.inputRow}
              error={Styles.error}
            />
            <TextInput
              label=""
              type="number"
              name="weight"
              placeholder="  "
              meta={getFieldMeta("weight")}
              {...getFieldProps("weight")}
              className={Styles.inputRow}
              error={Styles.error}
            />
            <div className={Styles.controls}>
              <button
                onClick={() => resetForm(initialValues)}
                className={Styles.clearData}
                disabled={!dirty || !isValid}
              >
                
              </button>
              <button
                type="submit"
                disabled={!dirty || !isValid}
              >
                
              </button>
            </div>
          </div>
        </form>
      </div>
      <div className={Styles.right}></div>
    </section>
  );
};
