import * as Yup from 'yup';

export const validationSchema = Yup.object().shape({
  fname: Yup.string(),
  lname: Yup.string(),
  email: Yup.string()
    .email(" ")
    .required("  "),
  password: Yup.string()
    .min(6, "  ")
    .required("  "),
  sex: Yup.string().required("  "),
  age: Yup.number(),
  height: Yup.number(),
  weight: Yup.number().required("  "),
});