import React from 'react';
import { NavLink } from 'react-router-dom';
import cx from 'classnames';

import Styles from '../styles/index.module.scss';

import { useQuestionList } from '../hooks/useQuestionList';

export const QuestionListJsx = () => {
    const {handleClick} = useQuestionList();

    const description = [
        {linkTo: "breakfast", style: "category1", title: " ", description: "   "},
        {linkTo: "lunch", style: "category2", title: " ", description: "  "},
        {linkTo: "dinner", style: "category3", title: " ", description: "   "},
        {linkTo: "steps", style: "category4", title: " ", description: "   "},
        {linkTo: "fruits", style: "category5", title: " ", description: "  "},
        {linkTo: "vegetables", style: "category6", title: " ", description: "  "},
        {linkTo: "junk", style: "category7", title: " ", description: "   "},
        {linkTo: "water", style: "category8", title: " ", description: "  "},
        {linkTo: "sleep", style: "category9", title: " ", description: "  "},
        {linkTo: "coffee", style: "category10", title: " ", description: "   "},
      ];

      const questionsJsx = description.map((item, index) => {

          return (
            <NavLink
              key={index}
              to={`/${item.linkTo}`}
              className={cx([Styles.link, Styles[item.style]])}
              onClick={() => handleClick(item.linkTo)}
            >
              <span className={Styles.title}>{item.title}</span>
              <span className={Styles.description}>{item.description}</span>
            </NavLink>
          );
      });

    return (
        <>
            {questionsJsx}
        </>
    )
};